import {GetSchoolCourseList} from '@/api/school'
export default {
	props:{
		schoolid:Number,
		costype:Number
	},
	data(){
		return{
			courseList:[],
			getParameters:{
				schoolid:this.schoolid,
				pageindex:1,
				pagesize:10,
				costype:this.costype//	课程类型
			},
			pageCount:0,
			refreshStatus:false,
			height:0,
			isshowprice:false
		}
	},
	watch:{
		'costype':{
			handler(costype){
				this.getParameters.costype = this.costype
				this.loadPage()
			},
			deep:true
		},
	},
	mounted(){
		uni.showLoading({
		    title: '加载中'
		});
		uni.getSystemInfo({
			success:res=>{
				this.height = res.windowHeight-226
			}
		})
		this.loadPage()
	},
	methods: {
		onRefresh(){
			this.loadPage()
		},
		open(item){
			var url = encodeURIComponent(item.weburl)
			uni.navigateTo({
				url:'/pages/web/web?title='+item.courseName+'&photo='+item.photoUrl+'&url='+url
			})
		},
		scrolltolower(){
			this.getParameters.pageindex++
			if(this.getParameters.pageindex <= this.pageCount){
				this.loadPage('more')
			}
		},
		//初始数据的请求
		loadPage(status){
			this.isshowprice = this.$store.state.isshowprice
			if(this.loading){
				return
			}
			this.loading = true
			if(status=='more'){
				GetSchoolCourseList(this.getParameters).then(res=>{
					if(res.result){
						this.courseList = this.askList.concat(res.data)
					}
					this.loading = false
				})
			}else{
				this.getParameters.pageindex=1
				GetSchoolCourseList(this.getParameters).then(res=>{
					if(res.result){
						this.pageCount = res.pageCount
						this.courseList = res.data
					}else{
						this.courseList = []
						this.pageCount = 0
					}
					uni.hideLoading();
					this.loading = false
					setTimeout(()=>{
						this.refreshStatus = false
					},1000)
				})
			}
		}
		
	}
}
